import { MessagesError } from "../../constants/messages.api";
import prismaClient from "../../prisma";
import { formatterDateToIso } from "../../utils/formatters/formatterDate";
import { validatorPermissions } from "../../utils/validators/validatorPermissions";

interface IGetDetailsVoucher {
  idUserLogged: string;
  id: string;
  idOtherUser?: string | null; // Novo campo opcional
}

class GetDetailsVoucherService {
  async execute({
    id,
    idUserLogged,
    idOtherUser,
  }: IGetDetailsVoucher) {
    if (!id) {
      return {
        data: {
          message: "N칚o foi poss칤vel prosseguir com est치 a칞칚o, id do voucher n칚o informado",
          status: 400,
        },
      };
    }

    if (!idUserLogged) {
      return {
        data: {
          message: "N칚o foi poss칤vel prosseguir com est치 a칞칚o, id do respons치vel n칚o informado",
          status: 400,
        },
      };
    }

    // Verifica se o voucher existe
    const voucherExists = await prismaClient.purchases.findFirst({
      where: { id },
    });

    if (!voucherExists) {
      return {
        data: {
          message: "N칚o foi poss칤vel prosseguir, o voucher n칚o foi emitido e n칚o existe.",
          status: 404,
        },
      };
    }

    // Verifica se o usu치rio logado existe
    const userExists = await prismaClient.users.findFirst({
      where: { id: idUserLogged },
    });

    if (!userExists) {
      return {
        data: {
          message: "N칚o foi poss칤vel prosseguir com est치 a칞칚o, usu치rio respons치vel n칚o existe",
          status: 404,
        },
      };
    }

    // Verifica as permiss칫es do usu치rio
    const responseValidation = validatorPermissions({
      typeAccess: userExists.typeAccess,
    });

    // Se idOtherUser for fornecido, valida se o usu치rio logado tem permiss칚o
    if (idOtherUser && responseValidation === false) {
      return {
        data: {
          message: "Sua conta n칚o tem permiss칚o para buscar os detalhes do voucher de outro usu치rio.",
          status: 403,
        },
      };
    }
    // Define o `idUserToQuery` como `idOtherUser` se passado, caso contr치rio, usa `idUserLogged`
    const idUserToQuery = idOtherUser ?? idUserLogged;

    try {
      // Busca os detalhes do voucher
      const dataResponseVoucher = await prismaClient.purchases.findFirst({
        where: {
          id,
          idUser: idUserToQuery, 
        },
        select: {
          id: true,
          idUser: true,
          idMethodPayment: true,
          codePayment: true,
          methodsPayments:true, 
          codeReferencePayment: true,
          datePayment: true,
          totalPrice: true,
          status: true,
          products: true,
          created_At: true,
        },
      });

      if (!dataResponseVoucher) {
        return {
          data: {
            message: "N칚o foi poss칤vel prosseguir com est치 a칞칚o, essa compra n칚o existe!",
            status: 404,
          },
        };
      }
      
      // Formata os dados do voucher
      const dataDetailsVoucherFormated = {
        ...dataResponseVoucher,
        products: typeof dataResponseVoucher?.products === 'string' ? JSON.parse(dataResponseVoucher.products) : dataResponseVoucher.products,
        datePayment: formatterDateToIso(dataResponseVoucher.datePayment),
      };

      return {
        data: {
          items: {
            dataDetailsVoucherFormated,
          },
          status: 200,
        },
      };
    } catch (error: any) {
      return {
        data: {
          message: MessagesError.GetDetailsMessageError,
          error: error?.message,
          status: 500,
        },
      };
    }
  }
}

export { GetDetailsVoucherService };
